@import url("https://fonts.googleapis.com/css?family=Open+Sans");

.App {
  font-family: "Open Sans", sans-serif;
  text-align: center;
}

.form-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.form-container > button,
.form-container > .ms-Dropdown-container {
  width: 24%;
  margin: 5px 0;
}

button {
  -ms-flex-item-align: end;
      align-self: flex-end;
}

.description-container {
  width: 100%;
}

.description {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  border: 1px solid black;
  margin: 20px auto;
  padding: 20px;
  -ms-flex-direction: column;
      flex-direction: column;
  width: 250px;
}

.imageDescription {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: column;
      flex-direction: column;
}

.imageDescription > div {
  -ms-flex-item-align: start;
      align-self: flex-start;
  padding: 5px 0;
}

@media screen and (max-width: 600px) {
  .form-container > button,
  .form-container > .ms-Dropdown-container {
    width: 48%;
  }

  .description {
    margin: 20px auto;
  }
}



